const Element = require('../element')
const Pool = require('../../util/pool')
const cache = require('../../util/cache')

const pool = new Pool()

class NotSupport extends Element {
    /**
     * åå»ºå®ä¾
     */
    static $$create(options, tree) {
        const config = cache.getConfig()

        if (config.optimization.elementMultiplexing) {
            // å¤ç¨ element èç¹
            const instance = pool.get()

            if (instance) {
                instance.$$init(options, tree)
                return instance
            }
        }

        return new NotSupport(options, tree)
    }

    /**
     * è¦åç¶ç±»ç $$init æ¹æ³
     */
    $$init(options, tree) {
        super.$$init(options, tree)

        // å¤çç¹æ®èç¹
        const window = cache.getWindow(this.$_pageId)
        if (window.onDealWithNotSupportDom) window.onDealWithNotSupportDom(this)
    }

    /**
     * è¦åç¶ç±»çåæ¶å®ä¾æ¹æ³
     */
    $$recycle() {
        this.$$destroy()

        const config = cache.getConfig()

        if (config.optimization.elementMultiplexing) {
            // å¤ç¨ element èç¹
            pool.add(this)
        }
    }

    get behavior() {
        return 'not-support'
    }
}

module.exports = NotSupport
