const Element = require('../element')
const cache = require('../../util/cache')
const Pool = require('../../util/pool')
const tool = require('../../util/tool')

const pool = new Pool()

class HTMLAnchorElement extends Element {
    /**
     * åå»ºå®ä¾
     */
    static $$create(options, tree) {
        const config = cache.getConfig()

        if (config.optimization.elementMultiplexing) {
            // å¤ç¨ element èç¹
            const instance = pool.get()

            if (instance) {
                instance.$$init(options, tree)
                return instance
            }
        }

        return new HTMLAnchorElement(options, tree)
    }

    /**
     * è¦åç¶ç±»ç $$init æ¹æ³
     */
    $$init(options, tree) {
        super.$$init(options, tree)

        this.$_protocol = 'http:'
        this.$_hostname = ''
        this.$_port = ''
        this.$_pathname = '/'
        this.$_search = ''
        this.$_hash = ''
    }

    /**
     * è¦åç¶ç±»ç $$destroy æ¹æ³
     */
    $$destroy() {
        super.$$destroy()

        this.$_protocol = null
        this.$_hostname = null
        this.$_port = null
        this.$_pathname = null
        this.$_search = null
        this.$_hash = null
    }

    /**
     * è¦åç¶ç±»çåæ¶å®ä¾æ¹æ³
     */
    $$recycle() {
        this.$_children.forEach(child => child.$$recycle())
        this.$$destroy()

        const config = cache.getConfig()

        if (config.optimization.elementMultiplexing) {
            // å¤ç¨ element èç¹
            pool.add(this)
        }
    }

    /**
     * è°ç¨ $_generateHtml æ¥å£æ¶ç¨äºå¤çé¢å¤çå±æ§ï¼
     */
    $$dealWithAttrsForGenerateHtml(html, node) {
        const href = node.href
        if (href) html += ` href="${tool.escapeForHtmlGeneration(href)}"`

        const target = node.target
        if (target) html += ` target="${tool.escapeForHtmlGeneration(target)}"`

        return html
    }

    /**
     * è°ç¨ outerHTML ç setter æ¶ç¨äºå¤çé¢å¤çå±æ§
     */
    $$dealWithAttrsForOuterHTML(node) {
        this.href = node.href || ''
        this.target = node.target || ''
    }

    /**
     * è°ç¨ cloneNode æ¥å£æ¶ç¨äºå¤çé¢å¤çå±æ§
     */
    $$dealWithAttrsForCloneNode() {
        return {
            href: this.href,
            target: this.target,
        }
    }

    get protocol() {
        return this.$_protocol
    }

    get hostname() {
        return this.$_hostname
    }

    get port() {
        return this.$_port
    }

    get pathname() {
        return this.$_pathname
    }

    get search() {
        return this.$_search
    }

    get hash() {
        return this.$_hash
    }

    get target() {
        return this.$_attrs.get('target')
    }

    set target(value) {
        value = '' + value
        this.$_attrs.set('target', value)
    }
}

module.exports = HTMLAnchorElement
